
.maxhr {
  margin-top: 20px;
  margin-bottom: 0;
  height: 8px;
  background: #eaeaea;
  border: 0;
  z-index: 8;
}
.maxhr :before {
  content: '';
  width: 100%;
  position: absolute;
  background: #eaeaea;
  z-index: 9;
  height: 8px;
}
@media screen and (min-width: 1000px) {
.maxhr {
    display: none;
}
}
.minhr {
  margin-top: 20px;
  margin-bottom: 0px;
  height: 3px;
  background: #eaeaea;
  border: 0;
}
@media screen and (min-width: 1000px) {
.minhr {
    display: none;
}
}
a {
  text-decoration: none !important;
  color: #555;
}
p,
a {
  font-family: "Helvetica Neue", "SimHei";
}
@media screen and (min-width: 1000px) {
#app:before {
    content: '';
    width: 100%;
    height: 15px;
    position: absolute;
    background-color: #f75341;
}
}
main {
  width: 100%;
}
main :after {
  content: '';
  display: block;
  clear: both;
}
.container {
  width: 100%;
  max-width: 1000px;
  position: relative;
  margin: 0 auto;
  box-sizing: border-box;
}
@media screen and (max-width: 1000px) {
.container {
    transform: translateY(-40px);
    padding: 0 0 8% 0%;
}
}
@media screen and (max-width: 1000px) {
.container p {
    margin: 5% 5%;
}
}
.container .item {
  width: 500px;
  height: 500px;
  float: left;
}


