
.navphone {
  height: 35px;
  width: 35px;
  background-image: linear-gradient(90deg, transparent);
  transform: translate(-18px, -18px);
  float: right;
  margin-right: 0;
  background: #2f2f2f;
  opacity: 0.9;
  width: 120%;
  height: 120%;
  position: fixed;
  z-index: 1000;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
@media screen and (min-width: 1000px) {
.navphone {
    display: none;
}
}
.navphone .phone {
  padding: 100px 15%;
}
.navphone .phone p,
.navphone .phone a {
  width: 85%;
  color: gray1;
  font-size: 22px;
  line-height: 40px;
}
.navphone .phone p {
  border-bottom: 1px solid #fff;
}
.navphone .chacha {
  cursor: pointer;
  width: 35px;
  height: 35px;
  background: url("../assets/chacha.png");
  position: fixed;
  left: 40px;
  top: 40px;
}


